/*Global widgets style*/

.mkdf-page-header,
.mkdf-top-bar,
.mkdf-sticky-header,
.mkdf-mobile-header {

	.widget {
		margin: 0;

		&.widget_pages,
		&.widget_archive,
		&.widget_categories,
		&.widget_meta,
		&.widget_recent_entries,
		&.widget_recent_comments,
		&.widget_rss,
		&.widget_calendar,
		&.widget_rss,
		&.widget_archive,
		&.widget_categories,
		&.widget_tag_cloud,
		&.widget_nav_menu,
		&.widget_search,
		&.widget_text {
			display: none !important;
		}
	}
}

.mkdf-sticky-header,
.mkdf-mobile-header {

	.widget {
		&.widget_text {
			display: none !important;
		}
	}
}