// widgets spacing
$widget-bottom-margin: 40px;
$widget-bottom-padding: 10px;
$widget-inner-padding: 10px;
$widget-title-margin: 19px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: transparent;

// list items border
$widget-list-item-border-color: #c9c9c9;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #000;
$widget-heading-color: #000;

// links
$widget-link-color: #000;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

footer {
    @import '../widget-types/default-wordpress-widgets';
    @import '../widget-types/default-wordpress-widgets-flat';
}

.mkdf-footer-top-inner.mkdf-full-width .mkdf-grid-col-12 {
    .widget {
        max-width: 720px;
        margin: 0 auto;
        }
}